@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300&display=swap);
*,
*::before,
*::after {
    margin: 0;
    box-sizing: border-box;
}

html {
    --ff-text: "Open Sans", sans-serif;
    --ff-title: "Noto Sans JP", sans-serif;
    --ff-code: "Fira Code", monospace;

    --clr-red: #e8064d;
    --clr-light: #ddd;
    --clr-white: #d4d4d4;
    --clr-grey: rgb(10, 10, 10);
    --clr-black: black;

    --fs-h1: 2.5rem;
    --fs-h2: 2.15rem;
    --fs-h3: 1.4rem;
    --fs-h4: 1.3rem;
    --fs-body: 1.1rem;

    height: 100vh;

    overflow-x: hidden;

    -ms-scroll-chaining: none;

        overscroll-behavior: none;

    scroll-behavior: smooth;
}

body {
    height: 100vh;
    font-family: "Open Sans", sans-serif;
    font-family: var(--ff-text);
    font-size: 1.1rem;
    font-size: var(--fs-body);
    font-weight: 300;
    color: #ddd;
    color: var(--clr-light);
    background-color: rgb(10, 10, 10);
    background-color: var(--clr-grey);
}

.App {
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.red {
    color: #e8064d;
    color: var(--clr-red);
}

h1,
h2,
h3,
h4 {
    line-height: 1.2;
    margin: 0;
}

h1 {
    font-size: 2.5rem;
    font-size: var(--fs-h1);
}

h2 {
    font-size: 2.15rem;
    font-size: var(--fs-h2);
}

h3 {
    font-size: 1.4rem;
    font-size: var(--fs-h3);
}

h4 {
    font-size: 1.3rem;
    font-size: var(--fs-h4);
}

@media (min-width: 800px) {
    :root {
        --fs-h1: 3rem;
        --fs-h2: 2.4rem;
        --fs-h3: 1.6rem;
        --fs-h4: 1.45rem;
        --fs-body: 1.25rem;
    }
}

@media (min-width: 1200px) {
    :root {
        --fs-h1: 3.65rem;
        --fs-h2: 3rem;
        --fs-h3: 1.9rem;
        --fs-h4: 1.75rem;
        --fs-body: 1.35rem;
    }
}

@media (min-width: 1500px) {
    :root {
        --fs-h1: 4.15rem;
        --fs-h2: 3.4rem;
        --fs-h3: 2.1rem;
        --fs-h4: 1.85rem;
        --fs-body: 1.4rem;
    }
}

@media (min-width: 1750px) {
    :root {
        --fs-h1: 4.25rem;
        --fs-h2: 3.5rem;
        --fs-h3: 2.25rem;
        --fs-h4: 1.9rem;
        --fs-body: 1.45rem;
    }
}

@media (min-width: 2000px) {
    :root {
        --fs-h1: 5rem;
        --fs-h2: 3.65rem;
        --fs-h3: 2.3rem;
        --fs-h4: 2rem;
        --fs-body: 1.5rem;
    }
}

@font-face {
    font-family: "Aero";
    src: url(/aeroclient/static/media/aero.364d576d.ttf);
}

a {
    text-decoration: none;
    color: inherit;
}

.docs {
    padding: 96px 2rem 325px 2rem;
    flex: 1 1;
    max-width: 1500px;
}

.docs ul li {
    padding: 0.5rem 0;
}

.documentation h3 {
    font-size: calc(var(--fs-h3) + 0.5rem);
}

.docs * {
    font-weight: 300;
}

.docs a {
    color: var(--clr-red);
}

.docs a:hover {
    text-decoration: underline;
}

.docs h1,
.docs h2,
.docs h3,
.docs h4 {
    font-family: var(--ff-title);
}

.docs p {
    line-height: 1.5;
    margin: 1.5rem 0;
}

.docs h2 {
    margin: 2rem 0;
}

.docs h3 {
    margin: 1.5rem 0;
}

.docs h4 {
    margin: 1.25rem 0;
}

@media (min-width: 900px) {
    .docs {
        padding-left: 325px;
        padding-bottom: 2rem;
    }
}

.header {
    z-index: 100;
    position: fixed;
    width: 100%;
    padding: 1.25rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--clr-black);
    border-bottom: 1px solid var(--clr-red);
}

.logo-container {
    display: grid;
    place-items: center;
}

.logo {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: 2rem;
    font-family: "Aero";
    color: var(--clr-white);
}

.nav {
    display: flex;
    font-family: var(--ff-text);
    font-size: 1.5rem;
    font-weight: 400;
}

.nav a {
    text-decoration: none;
    transition: 0.3s ease filter;
    margin: 0.25rem 1rem;
}

.nav a:hover {
    -webkit-filter: brightness(1.25);
            filter: brightness(1.25);
}

i.fa-npm {
    transform: scale(1.5);
}

pre {
    margin: 1rem 0;
    padding: 1rem;
    border-radius: 8px;
    background-color: rgb(24, 24, 24);
    overflow: scroll;
    max-height: 500px;
    white-space: pre-wrap;
    -moz-tab-size: 4;
         tab-size: 4;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

pre::-webkit-scrollbar {
    display: none;
}

pre code {
    color: #6fffe7;
    line-height: 1.5;
    font-size: calc(var(--fs-body) - 0.1rem);
}

code {
    font-family: var(--ff-code);
}

/*
 * Visual Studio 2015 dark style
 * Author: Nicolas LLOBERA <nllobera@gmail.com>
 */

.hljs-keyword,
.hljs-literal,
.hljs-symbol,
.hljs-name {
    color: #8aa5ff;
}

.hljs-link {
    color: var(--clr-red);
    text-decoration: underline;
}

.hljs-built_in,
.hljs-type {
    color: #5f87f6;
}

.hljs-number,
.hljs-class {
    color: #ab6bff;
}

.hljs-string,
.hljs-meta-string {
    color: #00ff9d;
}

.hljs-regexp,
.hljs-template-tag {
    color: #9a5334;
}

.hljs-function {
    color: #6fffe7;
}

.hljs-subst,
.hljs-title,
.hljs-params,
.hljs-formula {
    color: #5f87f6;
}

.hljs-comment,
.hljs-quote {
    color: #aaaaaa;
    font-style: italic;
}

.hljs-doctag {
    color: #8aa5ff;
}

.hljs-meta,
.hljs-meta-keyword,
.hljs-tag {
    color: #9b9b9b;
}

.hljs-variable,
.hljs-template-variable {
    color: #6fffe7;
}

.hljs-attr,
.hljs-attribute,
.hljs-builtin-name {
    color: #9cdcfe;
}

.hljs-section {
    color: gold;
}

.hljs-emphasis {
    font-style: italic;
}

.hljs-strong {
    font-weight: bold;
}

.hljs-bullet,
.hljs-selector-tag,
.hljs-selector-id,
.hljs-selector-class,
.hljs-selector-attr,
.hljs-selector-pseudo {
    color: #d7ba7d;
}

.hljs-addition {
    background-color: #144212;
    display: inline-block;
    width: 100%;
}

.hljs-deletion {
    background-color: #600;
    display: inline-block;
    width: 100%;
}

.sidebar {
    position: fixed;
    height: 300px;
    bottom: 0;
    width: 100%;
    padding: 1.25rem 0.75rem;
    background-color: var(--clr-black);
    border-top: 1px solid var(--clr-red);
    overflow-y: scroll;
    z-index: 10;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.sidebar::-webkit-scrollbar {
    display: none;
}

.sidebar-links a {
    line-height: 1.5;
    font-size: 1.5rem;
}

.t {
    margin-left: 2rem;
}

.sidebar a {
    position: relative;
    color: var(--clr-red);
}

.sidebar a::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    bottom: 0;
    left: 0;
    background-color: var(--clr-red);
    visibility: hidden;
    transform: translateX(-50%) scaleX(0);
    transition: all 0.2s ease-in-out 0s;
}

.sidebar a:hover::before {
    visibility: visible;
    transform: translateX(0) scaleX(1);
}

@media (min-width: 900px) {
    .sidebar {
        position: fixed;
        top: 68px;
        bottom: 0;
        height: unset;
        width: 300px;
        padding: 1.25rem 0.75rem;
        background-color: var(--clr-black);
        border-right: 1px solid var(--clr-red);
        overflow: scroll;
    }
}

